.icecream-modal__backdrop,
.coffee-modal__backdrop,
.milkshake-modal__backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: opacity 250ms var(--cubic);
  background-color: rgba(0, 0, 0, 0.2);
  overflow-y: scroll;
  &.is-hidden {
    opacity: 0;
    pointer-events: none;
  }
}
.icecream-modal__backdrop.is-hidden .icecream__modal,
.coffee-modal__backdrop.is-hidden .coffee__modal,
.milkshake-modal__backdrop.is-hidden .milkshake__modal {
  transform: translate(-50%, -50%) scale(0.9);
}

.icecream__modal,
.coffee__modal,
.milkshake__modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -30%) scale(1);
  transition: transform 250ms var(--cubic);
}
// upwards are the styles for modal javacript

.modal-ingredients {
  position: relative;
  display: block;
  max-width: 280px;
  padding: 28px 20px;
  background-color: var(--modalBG);
  border: 2px solid var(--yellowText);
  border-radius: 10px;
  @include tablet {
    max-width: 568px;
    padding: 32px 40px;
  }
  @include pc {
    max-width: 568px;
    padding: 32px 40px;
  }

  &__title {
    font-family: 'Seymour One';
    font-weight: 400;
    font-size: 20px;
    line-height: 1.4;
    text-align: center;
    margin-bottom: 22px;
  }

  &__img-box {
    background-color: var(--tintIcecream);
    border-radius: 50%;
    width: 132px;
    height: 138px;
    margin-bottom: 24px;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0px 5.26027px 19.726px rgba(10, 136, 23, 0.25);
  }

  &__img-box--coffee {
    background-color: var(--tintIcecoffee);
    box-shadow: 0px 5.26027px 19.726px rgba(153, 114, 23, 0.25);
  }

  &__img-box--milkshake {
    background-color: var(--tintMilkshake);
    box-shadow: 0px 5.26027px 19.726px rgba(0, 0, 0, 0.25);
  }

  &__img {
    width: 130px;
    height: 131px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 50%;
  }

  &__list {
    display: flex;
    justify-content: center;
    gap: 9px;
    margin-bottom: 39px;
  }

  &__item {
    width: 53px;
    height: 72px;
    border: 2px solid var(--modalGreen);
    border-radius: 40px;
    @include tablet {
      width: 65px;
      height: 90px;
    }
    @include pc {
      width: 65px;
      height: 90px;
    }
  }

  &__item--coffee {
    border: 2px solid var(--ice-coffee);
  }

  &__item--milkshake {
    border: 2px solid var(--milkshakes);
  }

  &__text {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 10px;
    line-height: 2.8;
  }

  &__text--icecream {
    background-color: var(--modalGreen);
    margin-left: auto;
    margin-right: auto;
    margin-top: -2px;
    width: 43px;
    height: 43px;
    border-radius: 50%;
    @include tablet {
      width: 53px;
      height: 53px;
    }
    @include pc {
      width: 53px;
      height: 53px;
    }
  }

  &__text--coffee {
    background-color: var(--ice-coffee);
    margin-left: auto;
    margin-right: auto;
    margin-top: -2px;
    width: 43px;
    height: 43px;
    border-radius: 50%;
    @include tablet {
      width: 53px;
      height: 53px;
    }
    @include pc {
      width: 53px;
      height: 53px;
    }
  }

  &__text--milkshake {
    background-color: var(--milkshakes);
    margin-left: auto;
    margin-right: auto;
    margin-top: -2px;
    width: 43px;
    height: 43px;
    border-radius: 50%;
    @include tablet {
      width: 53px;
      height: 53px;
    }
    @include pc {
      width: 53px;
      height: 53px;
    }
  }
}

.ingredients {
  margin-bottom: 48px;

  &__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 1.4;
    text-align: center;
    margin-bottom: 24px;
    color: var(--blackText);
  }

  &__item {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    line-height: 1.79;
    letter-spacing: 0.04em;
    color: var(--blackText);
    margin-bottom: 11px;
  }

  &__item:last-child {
    margin-bottom: 0;
  }
}

.form {
  @include tablet {
    padding-left: 60px;
    padding-right: 60px;
  }
  @include pc {
    padding-left: 60px;
    padding-right: 60px;
  }

  &__label {
    display: block;
    margin-bottom: 16px;
  }

  &__input:hover,
  &__input:focus,
  &__textarea:hover,
  &__textarea:focus {
    outline: 2px solid var(--accentBTN);
    border-color: var(--accentBTN);
  }

  &__input,
  &__textarea {
    display: block;
    width: 100%;
    border: 1px solid var(--mainBG);
    border-radius: 10px;
    background-color: var(--formBG);
  }

  &__input--coffee,
  &__textarea--coffee {
    border: 1px solid var(--ice-coffee);
  }

  &__input--milkshake,
  &__textarea--milkshake {
    border: 1px solid var(--milkshakes);
  }

  &__input {
    height: 42px;
    // margin-bottom: 16px;
  }

  &__textarea {
    height: 84px;
    margin-bottom: 32px;
  }

  &__input::placeholder,
  &__textarea::placeholder {
    padding-left: 10px;
    padding-top: 11px;
    font-size: 14px;
    line-height: 1.43;
    color: var(--blackText);
  }
}

.button {
  display: block;
  width: 132px;
  height: 42px;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.2;
  margin-left: auto;
  margin-right: auto;
  background: var(--yellowText);
  border-radius: 10px;
  border-color: transparent;
  @include tablet {
    width: 121px;
    height: 42px;
  }
  @include pc {
    width: 121px;
    height: 42px;
  }
}

.button-modal {
  position: absolute;
  padding: 0;
  top: 28px;
  right: 20px;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border-color: transparent;
  cursor: pointer;

  &__svg {
    fill: currentColor;
    transition: fill var(--duration) var(--cubic);
  }

  &__svg:hover,
  &__svg:focus {
    fill: var(--accentBTN);
  }
}

.button:hover {
  background-color: var(--accentBTN);
  transition: color var(--duration) var(--cubic),
    background-color var(--duration) var(--cubic);
}

.button:focus {
  color: var(--whiteText);
  background-color: var(--accentBTN);
}
